* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

.cursor-p {
  cursor: pointer;
}

.mt-sm {
  margin-top: .5rem !important;
}

.mt-md {
  margin-top: 1rem !important;
}

html {
  background-color: #6c946f80;
  font-size: 62.5%;
}

body {
  color: #ccc;
  background-color: #000;
  width: 41.2rem;
  height: 91.5rem;
  max-height: 100vh;
  margin: auto;
  font-family: Open Sans, sans-serif;
  font-size: 1.6rem;
}

.chat {
  height: 100%;
  overflow: auto;
}

.contacts {
  padding: 1rem;
}

.title {
  text-align: center;
  padding-top: 1rem;
  font-size: 2.4rem;
  font-style: oblique;
}

.messages {
  flex-direction: column;
  height: calc(100% - 13rem);
  margin-top: 7rem;
  padding: 0 1rem;
  display: flex;
  overflow: scroll;
}

.contactCard, .selected, .groupCard {
  background-color: #1a1a1a;
  border-radius: .5rem;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 1rem;
  padding: 1rem;
  display: flex;
}

.selected {
  width: 41.2rem;
  padding: 1rem;
  position: fixed;
  top: 0;
}

.contactCard .name, .groupCard .name, .selected .name {
  font-size: 2rem;
  font-weight: 600;
}

.selected .name {
  font-weight: 500;
}

.dp {
  border-radius: 50%;
  width: 5rem;
}

.letter-dp {
  color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 4.5rem;
  height: 4.5rem;
  font-size: 3.2rem;
  font-weight: 800;
  display: flex;
}

.arrow {
  width: 2.5rem;
  margin-left: auto;
}

.back.arrow {
  margin-left: 0;
  transform: rotate(180deg);
}

.input-box {
  font-size: inherit;
  color: inherit;
  background: #1a1a1a;
  border: none;
  border-radius: 10rem;
  outline: none;
  font-family: inherit;
}

.input-box::placeholder {
  color: #999;
}

.send-form {
  z-index: 100;
  background-color: #000;
  width: 41.2rem;
  padding: 1rem 1rem 2rem;
  display: flex;
  position: fixed;
  bottom: 0;
}

.input-box.send {
  background-size: 3rem;
  width: 100%;
  padding: 1rem 7rem 1rem 2rem;
}

.send-btn {
  width: 4rem;
  margin-left: -6rem;
}

.search-form {
  background-color: #000;
  padding: 1rem;
  position: sticky;
  top: 0;
}

.input-box.search {
  background-image: url("search.452f5fbe.svg");
  background-position: 95%;
  background-repeat: no-repeat;
  background-size: 2.3rem;
  width: 100%;
  margin-top: 1rem;
  padding: 1rem 5rem 1rem 2rem;
  font-size: 1.8rem;
}

.msg {
  width: auto;
  max-width: 80%;
  margin: 0 1rem;
  padding: .8rem 1.2rem;
  border-radius: 1.8rem !important;
}

.start-conversation {
  color: #666;
  margin: auto;
  font-weight: 800;
}

.sender-name {
  max-width: 100%;
  font-size: 1.2rem;
  font-weight: 800;
  display: block;
}

.sent-at {
  float: right;
  margin-top: 1rem;
  margin-left: 1rem;
  font-size: 1.2rem;
}

.msg-right {
  border-bottom-right-radius: unset;
  background-color: #ffd35a80;
  margin-left: auto;
}

.msg-left {
  border-bottom-left-radius: unset;
  background-color: #ffc7ed80;
  margin-right: auto;
}

.msg:first-child {
  margin-top: auto !important;
}

.last-msg {
  color: #b3b3b3;
  font-size: 1.4rem;
}

::-webkit-scrollbar {
  width: .5rem;
}

::-webkit-scrollbar-track {
  display: none;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 2rem;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media screen and (width <= 50em) {
  body {
    width: unset;
    max-height: unset;
    height: 100vh;
  }

  .send-form, .selected {
    width: 100%;
  }
}
